<template>
  <TheoryLayout title="Stack Pointer">
    <div class="theory-content">
        <h2>Was ist der Stack Pointer?</h2>
              <p>Der Stack Pointer (SP) ist ein spezielles Register in der JVM, das auf die aktuelle Position im Stack zeigt. Er ist wie ein Lesezeichen, das angibt, wo im Ausfhrungsstack wir uns gerade befinden.</p>
              
              <div class="visual-container">
                <img src="/images/theory/stack-pointer.png" alt="Stack Pointer Visualisierung" class="theory-image" onerror="this.style.display='none'; document.getElementById('stack-pointer-fallback').style.display='block';" />
                <div id="stack-pointer-fallback" class="visual-fallback" style="display:none;">
                  <pre>
          
            lokale Variable z = 7  
            lokale Variable y = 3  
            Parameter x = 5          Stack Pointer (SP)
          
            Rcksprungadresse      
            lokale Variable b = 42 
            lokale Variable a = 10 
          
                  </pre>
                </div>
              </div>

              <h3>Funktionsweise des Stack Pointers:</h3>
              <p>Der Stack Pointer ist ein dynamischer Zeiger, der sich mit jedem Methodenaufruf und jeder Methodenrckkehr im Speicher bewegt.</p>
              
              <div class="function-steps">
                <div class="step">
                  <h4>Bei Methodenaufrufen</h4>
                  <p>Wenn eine Methode aufgerufen wird:</p>
                  <ol>
                    <li>Wird ein neuer Stack-Frame erstellt</li>
                    <li>Der Stack Pointer "wandert" zum neuen Frame</li>
                    <li>Der neue Frame wird zum aktiven Ausfhrungskontext</li>
                  </ol>
                </div>
                
                <div class="step">
                  <h4>Bei Methodenrckkehr</h4>
                  <p>Wenn eine Methode beendet wird:</p>
                  <ol>
                    <li>Der aktuelle Frame wird entfernt</li>
                    <li>Der Stack Pointer "wandert zurck" zum vorherigen Frame</li>
                    <li>Die aufrufende Methode wird wieder aktiv</li>
                  </ol>
                </div>
              </div>

              <div class="animation-container">
                <h3>Der Stack Pointer in Aktion:</h3>
                <div class="stack-animation-steps">
                  <div class="animation-step">
                    <div class="step-description">1. <code>main()</code> wird ausgefhrt</div>
                    <pre>
          
            lokale Variable n = 5    SP
           
            Stack Frame: main()     
          
                    </pre>
                  </div>
                  
                  <div class="animation-step">
                    <div class="step-description">2. <code>calculate(n)</code> wird aufgerufen</div>
                    <pre>
          
            Parameter x = 5        
            lokale Variable res = 0  SP
          
            Rcksprungadresse      
            lokale Variable n = 5  
           
            Stack Frame: main()    
          
                    </pre>
                  </div>
                  
                  <div class="animation-step">
                    <div class="step-description">3. <code>calculate()</code> kehrt zurck</div>
                    <pre>
          
            lokale Variable n = 5  
            lokale Variable res = 25  SP (Ergebnis gespeichert)
           
            Stack Frame: main()    
          
                    </pre>
                  </div>
                </div>
              </div>

              <div class="example-box">
                <h4>Praktisches Beispiel zur Stack Pointer Bewegung:</h4>
                <pre class="code-example">
          public class StackPointerDemo {
              public static void main(String[] args) {
                  // SP zeigt auf den main()-Stack-Frame
                  int number = 10;
                  System.out.println("In main: " + number);
                  
                  // Methodenaufruf - SP wird auf firstMethod() bewegt
                  int result = firstMethod(number);
                  
                  // SP zeigt wieder auf den main()-Stack-Frame
                  System.out.println("Ergebnis: " + result);
              }
              
              public static int firstMethod(int x) {
                  // SP zeigt jetzt auf den firstMethod()-Stack-Frame
                  int y = x * 2;
                  System.out.println("In firstMethod: " + y);
                  
                  // Weiterer Methodenaufruf - SP bewegt sich zu secondMethod()
                  int z = secondMethod(y);
                  
                  // SP zeigt wieder auf den firstMethod()-Stack-Frame
                  return z + 5;
              }
              
              public static int secondMethod(int a) {
                  // SP zeigt jetzt auf den secondMethod()-Stack-Frame
                  int b = a + 3;
                  System.out.println("In secondMethod: " + b);
                  
                  // Rckkehr - SP geht zurck zum firstMethod()-Frame
                  return b * 2;
              }
          }</pre>
                
                <div class="stack-visualization">
                  <p><strong>Stack Pointer Verschiebung whrend der Ausfhrung:</strong></p>
                  <div class="animation-steps">
                    <div class="step">
                      <div class="step-header">main() wird ausgefhrt</div>
                      <pre>
          
           number = 10         SP hier
          </pre>
                    </div>
                    <div class="step">
                      <div class="step-header">firstMethod(number) wird aufgerufen</div>
                      <pre>
          
           x = 10            
           y = 20              SP bewegt hierher
          
           number = 10       
          </pre>
                    </div>
                    <div class="step">
                      <div class="step-header">secondMethod(y) wird von firstMethod aufgerufen</div>
                      <pre>
          
           a = 20            
           b = 23              SP bewegt hierher
          
           x = 10            
           y = 20            
          
           number = 10       
          </pre>
                    </div>
                    <div class="step">
                      <div class="step-header">secondMethod() kehrt zurck (b*2 = 46)</div>
                      <pre>
          
           x = 10            
           y = 20            
           z = 46              SP bewegt hierher zurck
          
           number = 10       
          </pre>
                    </div>
                    <div class="step">
                      <div class="step-header">firstMethod() kehrt zurck (z+5 = 51)</div>
                      <pre>
          
           number = 10       
           result = 51         SP bewegt hierher zurck
          </pre>
                    </div>
                  </div>
                </div>
              </div>

              <div class="important-note">
                <h4>Wichtig zu wissen:</h4>
                <ul>
                  <li><strong>Thread-spezifisch:</strong> Jeder Java-Thread hat seinen eigenen Stack und damit auch seinen eigenen Stack Pointer.</li>
                  <li><strong>Nicht direkt zugreifbar:</strong> Im Gegensatz zu einigen Programmiersprachen ist der Stack Pointer in Java nicht direkt ber Code manipulierbar.</li>
                  <li><strong>StackOverflowError:</strong> Wenn zu viele verschachtelte Methodenaufrufe erfolgen und der Stack seine maximale Gre erreicht, kann der Stack Pointer nicht weiter verschoben werden, was zu einem StackOverflowError fhrt.</li>
                  <li><strong>Performance:</strong> Da der Stack Pointer immer auf den aktiven Frame zeigt, ist der Zugriff auf lokale Variablen sehr schnell - ein wichtiger Leistungsvorteil.</li>
                </ul>
              </div>

              <div class="summary-section">
                <h3>Zusammenfassung:</h3>
                <p>Der Stack Pointer ist ein essenzieller Mechanismus der JVM, der den aktuellen Ausfhrungskontext verfolgt. Er bewegt sich mit jedem Methodenaufruf und jeder Methodenrckkehr und ermglicht so die strukturierte Abarbeitung von verschachtelten Methodenaufrufen. Ohne den Stack Pointer knnte die JVM nicht wissen, wo im Programm sie sich gerade befindet und welche Variablen zum aktuellen Kontext gehren.</p>
              </div>
              
              <h3>Zusammenspiel mit dem Call Stack:</h3>
              <p>Wenn eine neue Methode aufgerufen wird, wird ein neuer Stack-Frame erstellt und der Stack Pointer wird aktualisiert, um auf diesen neuen Frame zu zeigen. Wenn die Methode beendet wird, wird der Stack-Frame entfernt und der Stack Pointer wird zurckgesetzt, um auf den vorherigen Frame zu zeigen.</p>
    </div>
  </TheoryLayout>
</template>

