<template>
  <TheoryLayout title="Memory Heap">
    <div class="theory-content">
      <h2>Was ist der Memory Heap?</h2>
      <p>Der Memory Heap ist ein dynamischer Speicherbereich, der von JVM verwaltet wird. In ihm werden Arrays und Objekte von Klassen zur Laufzeit dynamisch erstellt und verwaltet.</p>
      <p>Anders als der Stack, der nur temporre Werte speichert, speichert der Heap die Daten langfristig.</p>
      
      <div class="analogy-box">
        <h3>Alltagsbeispiel</h3>
        <p>Stell dir den Heap wie eine grosse Bcherei vor, und den Stack wie deinen Schreibtisch:</p>
        <ul>
          <li><strong>Die Bcherei (Heap):</strong> Ein riesiger, gemeinsamer Raum, in dem alle Bcher (Objekte) aufbewahrt werden. Wenn die Bcherei ein neues Buch kauf (ein Objekt erstellst), wird es dort in den Regalen eingeordnet. Die Bcher bleiben so lange dort, bis sie niemand mehr braucht (keine Referenz mehr).</li>
          <li><strong>Dein Schreibtisch (Stack):</strong> Ein kleiner, persnlicher Bereich, auf dem du schnell Notizen (primitive Variablen) machst oder Liste von Bchern (Referenzen) ablegst, die du von der Bcherei ausleihen willst. Verlsst du den Raum (Methode endet), wird dein Schreibtisch komplett abgerumt, du siehst ihn nicht mehr.</li>
        </ul>
        <p>Da der Stack viel kleiner ist, als der Heap ist im Stack jeweils nur eine Referenz auf die Speicheradresse im Heap hinterlegt, wo sich das Objekt befindet.</p>
      </div>

      <div class="object-creation-section">
        <h3>Objekte im Heap: Neue Bcher in der Bcherei</h3>
        <p>Der Heap speichert alle Objekte, die mit <code>new</code> erstellt werden, sowie ihre Instanzvariablen.</p>
        
        <div class="two-column-layout">
          <div class="column">
            <pre class="code-example">
class Buch {
    int seitenzahl;
}

public class Main {
    public static void main(String[] args) {
        Buch meinBuch = new Buch();  // Speicherzuweisung im Heap
        meinBuch.seitenzahl = 350; // Referenz auf das Buch im Heap
    }
}</pre>
          </div>
          
          <div class="column">
            <div class="explanation-box">
              <ul>
                <li>Mit <code>new</code> wird ein neues <strong>Buch-Objekt</strong> erstellt.
                  Das ist wie ein neues Buch, das in der Bcherei (Heap) eingeordnet wird</li>
                <li>Die Variable <strong>meinBuch</strong> liegt im Stack und enthlt eine Referenz auf das Buch im Heap.
                  Das ist wie ein Ausleihzettel (im Stack), der auf das Buch im Regal zeigt.</li>
                <li>Die Eigenschaft <strong>seitenzahl</strong> ist eine Instanzvariable des Objekts.
                  Das ist wie die Anzahl der Seiten im Buch. Der Wert <strong>350</strong> wird direkt im Buch gespeichert, man sieht erst wie viele Seitenzahlen das Buch hat wenn man es ffnet.</li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div class="two-column-layout align-items-start">
        <div class="column">
          <div class="content-box">
            <h3>Inhalte des Heaps:</h3>
            <ul>
              <li><strong>Objekte:</strong> Instanzen von Klassen (neue Objekte mit <code>new</code> erstellt)</li>
              <li><strong>Arrays:</strong> Auch wenn sie primitive Werte enthalten, ist das Array-Objekt selbst im Heap</li>
              <li><strong>Strings:</strong> String-Objekte werden im Heap gespeichert (mit String-Pool-Optimierungen)</li>
              <li><strong>Komplexe Datenstrukturen:</strong> Listen, Maps und alle anderen Kollektionen</li>
              <li><strong>Klasseninstanzen</strong> mit ihren Instanzvariablen</li>
              <li><strong>Instanzvariablen:</strong> Die Felder/Attribute eines Objekts</li>
            </ul>
          </div>
        </div>  
        
        <div class="column">
          <div class="visual-container">
        <div class="visual-fallback">
          <pre>

            HEAP              
                              
     
   String       Person    
   "Hallo"     name="Max" 
    alter=25   
                  
                 
   int[]        
   [3,1,4,2]    ArrayList 
     
                              

          </pre>
        </div>
      </div>
        </div>   
      </div>
      
        
      <div class="example-box">
        <h4>Praktisches Bcherei-Beispiel:</h4>
        <p>Denk an ein Bibliotheks-Management-System. Hier sehen wir, wie verschiedene Informationen im Heap gespeichert werden:</p>
        <pre class="code-example">
public class BibliothekenBeispiel {
    public static void main(String[] args) {
        // Ein Buch-Objekt wird im Heap erstellt
        Buch buch1 = new Buch("Java fr Anfnger", "Programmierung");
        
        // Ein weiteres Buch im Heap
        Buch buch2 = new Buch("Algorithmen verstehen", "Informatik");
        
        // Array mit Bewertungen - das Array selbst ist ein Objekt im Heap
        double[] bewertungen = {3.8, 4.2, 4.5, 5.0};
        
        // Die ArrayList ist ein Objekt im Heap
        ArrayList&lt;String&gt; ausleiher = new ArrayList&lt;&gt;();
        ausleiher.add("Max Mustermann"); // Jeder String ist auch ein Objekt im Heap
        ausleiher.add("Lisa Schmidt");
        
        // Bibliothek-Objekt mit Buch-Referenzen
        Bibliothek bibliothek = new Bibliothek("Stadtbibliothek");
        bibliothek.addBuch(buch1);
        bibliothek.addBuch(buch2);
        
        // Methode beenden: buch1, buch2, bewertungen, ausleiher und bibliothek 
        // bleiben im Heap, solange Referenzen bestehen
    }
}

class Buch {
    private String titel;    // Referenz zu einem String im Heap
    private String kategorie; // Referenz zu einem String im Heap
    
    public Buch(String titel, String kategorie) {
        this.titel = titel;
        this.kategorie = kategorie;
    }
}

class Bibliothek {
    private String name;
    private List&lt;Buch&gt; bcher = new ArrayList&lt;&gt;();
    
    public Bibliothek(String name) {
        this.name = name;
    }
    
    public void addBuch(Buch buch) {
        bcher.add(buch);
    }
}</pre>

        <div class="heap-visualization">
          <p><strong>So sieht der Heap-Speicher (unsere Bcherei) dafr aus:</strong></p>
          <pre>
BCHEREI (HEAP):

                                                          
            
   Buch            String              String       
   titel   "Java fr           "Program-    
   kateg.   Anfnger"           mierung"    
            
                                                          
            
   Buch            String              String       
   titel   "Algorithmen        "Informatik"  
   kateg.   verstehen"                      
            
                                                          
                                              
   double[]                                             
   [3.8,                                                
    4.2,                                                
    4.5,                                                
    5.0]                                                
                                              
                                                          
         
   ArrayList       String               String      
   size=2         | "Max Mustermann"     "Lisa       
   elements                       Schmidt"   
         
                                                          
                     
   Bibliothek      String                             
   name     "Stadtbibliothek"                  
   bcher                        
   (Liste mit                                           
   Referenzen)                                          
                                            

          </pre>
        </div>
        
        <div class="explanation-box">
          <ul>
            <li>
              <p>Die mit <code>new</code> erstellten <strong>Buch-Objekte</strong> <code>buch1</code> und <code>buch2</code> sind eigenstndige Objekte im Heap-Speicher.</p>
              <p>Jedes dieser Objekte besitzt Instanzvariablen, die als Referenzen auf die zugehrigen String-Objekte im Heap verweisen.</p>
              <p>Zum Beispiel:</p>
              <p>Das Buch-Objekt <code>buch1</code> enthlt eine Referenz auf den Titel <strong>"Java fr Anfnger"</strong> sowie auf die Kategorie <strong>"Programmierung"</strong>.</p>
              <p>Das Buch-Objekt <code>buch2</code> verweist entsprechend auf <strong>"Algorithmen verstehen"</strong> und <strong>"Informatik"</strong>.</p>
              <p>Diese Buch-Objekte kann man sich wie Bcher in einer Bibliothek vorstellen:</p>
              <p>Sie werden ins Regal gestellt (Heap), wo sie physisch vorhanden sind.</p>
              <p>Die Ausleihzettel (Referenzen) liegen im Verwaltungssystem (Stack), sodass man jederzeit auf die Bcher zugreifen kann.</p>
            </li>
            
            <li>
              <p>Die mit <code>new</code> erzeugte <strong>ArrayList</strong> <code>ausleiher</code> ist ebenfalls ein Objekt im Heap.</p>
              <p>Sie speichert Referenzen auf String-Objekte, die die Namen der Personen enthalten, die Bcher ausgeliehen haben.</p>
              <p>In diesem Fall zeigt die Liste auf die Strings <strong>"Max Mustermann"</strong> und <strong>"Lisa Schmidt"</strong>, die ebenfalls im Heap liegen.</p>
              <p>Dies entspricht einer Liste der Leser in der Bibliothek, die registriert sind und Bcher ausgeliehen haben.</p>
            </li>
            
            <li>
              <p>Das <strong>Array</strong> <code>bewertungen</code> ist ein weiteres Objekt im Heap, das Bewertungen der Bcher speichert.</p>
              <p>Es enthlt double-Werte wie [3.8, 4.2, 4.5, 5.0].</p>
              <p>Da Arrays in Java ebenfalls Objekte im Heap sind, wird das Array als Ganzes dort abgelegt.</p>
              <p>Dies kann man sich vorstellen wie einen Bewertungsbogen, der die Lesermeinungen ber Bcher sammelt.</p>
            </li>
            
            <li>
              <p>Das <strong>Bibliothek-Objekt</strong> <code>bibliothek</code> wird ebenfalls mit <code>new</code> erstellt und im Heap gespeichert.</p>
              <p>Es speichert den Namen "Stadtbibliothek" als Referenz auf ein String-Objekt im Heap.</p>
              <p>Zustzlich enthlt es eine Liste von Buch-Referenzen, die auf <code>buch1</code> und <code>buch2</code> zeigen.</p>
              <p>Diese Klasse ist vergleichbar mit dem Verwaltungssystem der Bibliothek, das den Bestand der Bcher organisiert.</p>
            </li>
          </ul>
          
          <h4>Zusammenfassung (Metapher)</h4>
          <ul>
            <li>Buch-Objekte (<code>buch1</code>, <code>buch2</code>) sind echte Bcher im Regal der Bibliothek (Heap).</li>
            <li>Titel und Kategorien sind Beschriftungen und Genres der Bcher.</li>
            <li>Array fr Bewertungen ist ein Bewertungsbogen, der Lesermeinungen speichert.</li>
            <li>ArrayList der Ausleiher ist eine Liste der Leser, die Bcher ausgeliehen haben.</li>
            <li>Bibliothek-Objekt entspricht dem Verwaltungssystem, das alle Bcher katalogisiert.</li>
            <li>Referenzvariablen im Stack (z. B. <code>buch1</code>, <code>buch2</code>) sind Ausleihzettel, die zeigen, wo die Bcher im Regal stehen.</li>
          </ul>

          <ul>
            <li><p>Die <strong>Buch-Objekte</strong> "buch1", "buch2" die mit <code>new</code> erstellt wurden, sind eigene Objekte im Heap.</p>
              <p>Jedes dieser Objekte enthlt die Instanzvariablen mit der Referenz auf den entsprechenden String.</p>
              <p>Beispielsweise enthlt das Buch-Objekt "buch1" die Referenz auf den String "Java fr Anfnger" und die Kategorie "Programmierung".</p>
            <p>Es sind wie Bcher, die in ein Regal der Bcherei gestellt werden, damit man sie ansehen kann.</p></li>
            <li>Die <strong>Bibliothek-Klasse</strong> ist wie das Verwaltungssystem der Bcherei</li>
            <li>Die <strong>Titel und Kategorien</strong> sind wie Beschriftungen und Genres der Bcher</li>
            <li>Das <strong>Bewertungs-Array</strong> ist wie ein Bewertungsbogen fr Bcher</li>
            <li>Die <strong>ArrayList</strong> fr Ausleiher ist wie eine Liste der Leser, die Bcher ausgeliehen haben</li>
          </ul>
          <p>Wenn die <code>main</code>-Methode endet, bleiben alle Bcher und Informationen im Heap (der Bcherei), solange noch Referenzen darauf existieren. Erst wenn keine "Ausleihzettel" (Referenzen) mehr vorhanden sind, kann der Bibliothekar (Garbage Collector) die Bcher aussortieren.</p>
        </div>
      </div>
      
      
      
      <h3>String-Pool: Eine Besonderheit im Heap</h3>
      <div class="two-column-layout">
        <div class="column">
          <div class="string-pool">
            <p>Java optimiert die Speicherung von Strings durch einen speziellen Bereich im Heap, den String-Pool:</p>
            <p>Stell dir den String-Pool wie ein besonderes Bcherregal nur fr hufig verwendete Bcher vor:</p>
            <pre class="code-example">
String s1 = "Java";       // Wird im String-Pool abgelegt
String s2 = "Java";       // Greift auf das gleiche Buch im Pool zu
String s3 = new String("Java");  // Erstellt eine neue Kopie des Buches ausserhalb des Pools

System.out.println(s1 == s2);     // true - beide haben denselben Ausweis
System.out.println(s1 == s3);     // false - unterschiedliche Ausweise
System.out.println(s1.equals(s3)); // true - gleicher Inhalt, aber verschiedene Exemplare</pre>

            <p>Mit dem befehl new String, kann im Heap ein String ausserhalb des String-Pools erstellt werden. Das ist, als wrden zwei Studenten (s1 und s2) das gleiche Bibliotheksbuch ausleihen, whrend ein dritter (s3) eine private Kopie kauft.</p>
          </div>
        </div>
        
        <div class="column">
          <div class="visual-container">
            <div class="visual-fallback">
              <pre>
HEAP:

                                 
            
     String-Pool               
                
      "Java"     s1    
           s2   
                               
            
                                 
                                 
              
      "Java"       s3  
              
                                 

              </pre>
            </div>
          </div>
        </div>
      </div>

      

      <div class="heap-stack-connection">
        <h3>Wie sind Heap und Stack verknpft?</h3>
        
        <div class="explanation-box">
          <ul>
            <li><strong>Lokale Variablen</strong> (primitive Typen & Referenzen) sind wie Notizen auf deinem persnlichen Zettel (Stack)</li>
            <li><strong>Objekte und Instanzvariablen</strong> sind wie die Bcher selbst und ihre Inhalte (im Heap)</li>
            <li><strong>Methodenende:</strong> Wenn du den Leseraum verlsst (Methode endet), wird dein Notizzettel weggeworfen (Stack-Frame gelscht), aber die Bcher bleiben in den Regalen (Heap), solange noch jemand einen Ausleihzettel dafr hat</li>
          </ul>
          <p>Das bedeutet: Solange ein Buch (Objekt oder Array) noch von irgendjemandem gelesen wird (eine Referenz hat), bleibt es in der Bcherei (Heap). Erst wenn niemand mehr darauf verweist, kann es aussortiert werden.</p>
        </div>
      </div>

      <div class="gc-example">
        <h4>Speicherfreigabe durch den Bibliothekar (Garbage Collector)</h4>
        <p>Beim Heap wird der Speicher nicht mehr verwendeter Objekte automatisch vom Garbage Collector freigegeben. Das bedeutet, wenn im Code keine Referenz mehr auf ein Objekt verweist, wird es als "unreachable" (unerreichbar) markiert und entfernt.</p>
        
        <div class="two-column-layout">
          <div class="column">
            <pre class="code-example">
public class GCBeispiel {
    public static void main(String[] args) {
        // Buch wird erstellt und im Heap abgelegt
        Buch javaBuch = new Buch("Java Grundlagen");
        
        System.out.println(javaBuch.titel);
        
        // Referenz wird berschrieben
        javaBuch = new Buch("Java Fortgeschritten");
        
        // Das erste Buch hat keine Referenz mehr
        // und ist bereit fr den Garbage Collector
    }
}

class Buch {
    String titel;
    
    public Buch(String titel) {
        this.titel = titel;
    }
}</pre>
          </div>
          
          <div class="column">
            <div class="visual-container">
              <div class="visual-fallback">
                <pre>
Vor der Neuzuweisung:      Nach der Neuzuweisung:
           
 javaBuch                 javaBuch          
           
                                    
                                    
           
 Buch                     Buch              
"Java                    "Java              
 Grundlagen"              Fortgeschritten"  
           
                          
                           
                            Buch              
                           "Java Grundlagen"  
                           (keine Referenz)   
                            GC bereit        
                           </pre>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="gc-section">
        <h3>Heap-Speicherverwaltung und Garbage Collection:</h3>
        <p>Anders als der Stack wird der Heap nicht automatisch bei Methodenenden aufgerumt. Java nutzt die Garbage Collection, einen automatischen Prozess, der nicht mehr bentigten Speicher im Heap freigibt.</p>
        
        <div class="two-column-layout align-items-start">
          <div class="column">          
            <div class="gc-explanation">
              <h4>So funktioniert Garbage Collection - in Alltagsbegriffen:</h4>
              <p>Stell dir vor, der Heap ist eine groe Bibliothek und der Garbage Collector ein fleiiger Bibliothekar:</p>
              <ol>
                <li><strong>Identifikation:</strong> Der Bibliothekar prft, welche Bcher (Objekte) keine Ausweise (Referenzen) mehr haben, die auf sie zeigen.</li>
                <li><strong>Markieren:</strong> Bcher, die noch von jemandem ausgeliehen werden knnten (erreichbare Objekte), bekommen ein Lesezeichen.</li>
                <li><strong>Aussortieren/Lschen:</strong> Bcher ohne Lesezeichen (nicht erreichbare Objekte) werden aus den Regalen entfernt.</li>
                <li><strong>Neu ordnen:</strong> Die Regale werden neu geordnet, um Platz zu schaffen (Speicherkomprimierung).</li>
              </ol>
              <p>Dieser Prozess geschieht automatisch und in regelmigen Abstnden, aber kann manchmal zu kurzen Pausen in der Programmausfhrung fhren - hnlich wie wenn die Bibliothek kurz schliet, um aufzurumen.</p>
            </div>
          </div>
          
          <div class="column">
            <div class="visual-container gc-visual">
              <div class="visual-fallback">
                <pre>
Vor GC:              Nach GC:
           
 Obj A         Obj A  
               
                                   
               
 Obj B             Obj C       
               
                                   
                         
 Obj C  Referenz        
                         
                                   
          (Obj B wurde   
 Obj D            freigegeben,   
          Obj D auch)    
                                   
Referenzen      Referenzen
                </pre>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="memory-leaks-detail">
        <h3>Memory Leaks</h3>
        <p>Wenn Objekte unntig referenziert bleiben und somit nicht gelscht werden knnen, obwohl sie nicht mehr gebraucht werden, spricht man von einem Memory Leak (Speicherleck).</p>
        
        <div class="example-box">
          <h4>Alltagsbeispiel</h4>
          <p>Stell dir vor, ein Student hat einen Ausleihzettel fr ein Fachbuch, das er lngst nicht mehr liest. Er hat den Zettel in einer Schublade vergessen und verlsst die Universitt. Das Buch bleibt in der Bcherei und belegt wertvollen Platz, obwohl es niemand mehr braucht - aber der Bibliothekar kann es nicht entfernen, weil formal noch ein Ausleihzettel existiert.</p>
        </div>
      </div>

      <div class="important-note">
            <h4>Probleme:</h4>
            <ul>
              <li><strong>OutOfMemoryError:</strong> Wenn der Heap voll ist und die Garbage Collection nicht genug Speicher freigeben kann, wird ein OutOfMemoryError ausgelst - wie wenn deine Bcherei keinen Platz mehr fr neue Bcher hat.</li>
              <li><strong>Referenzen sind der Schlssel:</strong> Objekte werden nicht automatisch gelscht, wenn sie nicht mehr verwendet werden - sie mssen fr die Garbage Collection nicht mehr referenziert sein. Wie ein Buch, das niemand mehr ausleihen will.</li>
              <li><strong>Speicher-Leaks:</strong> Wenn Objekte unntig referenziert bleiben, knnen Speicherlecks entstehen, auch in Java. Das ist wie Bcher, die niemand mehr liest, aber trotzdem nicht aussortiert werden knnen, weil jemand sie theoretisch noch lesen knnte.</li>
            </ul>
          </div>
      
      <div class="summary-section">
        <h3>Zusammenfassung:</h3>
        <p>Der Heap ist wie eine grosse Bibliothek fr alle Objekte in einem Java-Programm. Im Gegensatz zum Stack, der sich automatisch leert, bentigt der Heap einen "Hausmeister" (den Garbage Collector), der regelmssig aufrumt.</p>
        <ul>
          <li>Mit <code>new</code> wird ein neues Objekt im Heap erstellt</li>
          <li>Referenzen zeigen auf Objekte - sie sind nicht die Objekte selbst</li>
          <li>Speicherplatz kann erst freigegeben werden, wenn keine Referenz mehr auf das Objekt zeigt</li>
        </ul>
      </div>
    </div>
