<template>
    <div class="code-explorer">
      <h2>Java Code</h2>
      <div class="code-container">
        <div 
          v-for="(line, index) in codeLines" 
          :key="index"
          :class="['code-line', { 'active-line': store.currentLine === index }]"
        >
          <!-- Instruction Pointer Pfeil -->
          <div 
            v-if="store.nextLineToExecute === index" 
            class="instruction-pointer-arrow"
          >
            
          </div>
          <div v-else class="line-spacer"></div>
          
          <span class="line-number">{{ index + 1 }}</span>
          <span class="line-content">{{ line }}</span>
        </div>
      </div>
      <div class="controls">
        <button @click="previousLine" class="btn btn-secondary" :disabled="!store.canGoBack">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <polyline points="15 18 9 12 15 6"></polyline>
          </svg>
          Previous Step
        </button>
        <button @click="nextLine" class="btn">
          Next Step
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <polyline points="9 18 15 12 9 6"></polyline>
          </svg>
        </button>
        <button @click="reset" class="btn btn-reset">Reset</button>
      </div>
    </div>
  </template>
  
  <script>
  import { useMemoryStore } from '../store/memory'
  import { computed, onMounted } from 'vue'
  
  export default {
    name: 'CodeExplorer',
    emits: ['next-step', 'prev-step', 'reset'],
    setup(props, { emit }) {
      const store = useMemoryStore()
      
      const codeLines = computed(() => store.currentCode.split('\n'))
      
      const nextLine = () => {
        const moved = store.nextLine()
        if (moved) {
          emit('next-step')
        }
      }
  
      const previousLine = () => {
        const moved = store.previousLine()
        if (moved) {
          emit('prev-step')
        }
      }
      
      const reset = () => {
        store.reset()
        emit('reset')
      }
      
      // Initialisieren
      onMounted(() => {
        store.reset() // Stellen wir sicher, dass wir mit einem sauberen Zustand starten
      })
      
      return {
        store,
        codeLines,
        nextLine,
        previousLine,
        reset
      }
    }
  }
  </script>
  
  <style scoped>
  .code-explorer {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    background-color: white;
  }
  
  .code-container {
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 4px;
    font-family: monospace;
    margin-bottom: 15px;
    max-height: 400px;
    overflow-y: auto;
  }
  
  .code-line {
    padding: 2px 5px;
    display: flex;
    align-items: center;
  }
  
  .active-line {
    background-color: #ffff99;
    font-weight: bold;
  }
  
  /* Styles fr den Instruction Pointer Pfeil */
.instruction-pointer-arrow {
  color: #22cfff;
  font-weight: bold;
  width: 20px;
  display: flex;
  justify-content: center;
  animation: blink 1.5s infinite;
  font-size: 14px;
  position: relative;
  cursor: help;
}
  
  .line-spacer {
    width: 20px; /* Gleiche Breite wie der Pfeil fr konsistentes Layout */
  }
  
  /* Animation fr den blinkenden Pfeil */
  @keyframes blink {
    0% { opacity: 0.8; }
    50% { opacity: 1; transform: scale(1.05); }
    100% { opacity: 0.8; }
  }
  
  .line-number {
    color: #999;
    text-align: right;
    padding-right: 10px;
    user-select: none;
    min-width: 30px;
  }
  
  .controls {
    display: flex;
    gap: 10px;
    justify-content: space-between;
  }
  
  .btn {
    padding: 8px 16px;
    background-color: #0550ae;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .btn:hover {
    background-color: #0550ae;
  }
  
  .btn-secondary {
    background-color: #2196F3;
  }
  
  .btn-secondary:hover {
    background-color: #0b7dda;
  }
  
  .btn-secondary:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .btn-reset {
    background-color: #f44336;
  }
  
  .btn-reset:hover {
    background-color: #d32f2f;
  }
  </style>