<template>
  <TheoryLayout title="Memory Stack">
    <div class="theory-content">
      <h2>Was ist der Memory Stack?</h2>
      <p>Der Memory Stack ist ein spezieller Speicherbereich in Java, der zur Laufzeit zugewiesen wird und primitive Datentypen sowie Referenzen auf Objekte speichert.</p>

      <div class="visual-container">
        <div class="visual-fallback">
          <pre>

 Stack Frame: main()        
                            
 int x = 5                  
 boolean flag = true        
 Student s1  [Heap: 0x1A2] 

          </pre>
        </div>
      </div>

      <h3>Inhalte des Memory Stack:</h3>
      <div class="content-grid">
          <ul>
            <li>Primitive Variablen (int, boolean, float, etc.)</li>
            <li>Referenzvariablen (Zeiger auf Objekte im Heap)</li>
            <li>Methodenparameter</li>
            <li>Rckgabewerte</li>
            <li>Temporre Zwischenergebnisse von Berechnungen</li>
          </ul>
      </div>

      <h3>Eigenschaften des Memory Stacks:</h3>
      <div class="content-grid">
        <div class="content-item">
          <h4>Automatische Verwaltung</h4>
          <p>Variablen werden erstellt und gelscht, wenn Methoden aufgerufen und beendet werden.</p>
        </div>
        <div class="content-item">
          <h4>Lebensdauer</h4>
          <p>Nur whrend der Methodenausfhrung vorhanden, danach wird der Speicher freigegeben.</p>
        </div>
        <div class="content-item">
          <h4>Geschwindigkeit</h4>
          <p>Schneller Zugriff durch das LIFO-Prinzip und vorhersehbare Allokation.</p>
        </div>
        <div class="content-item">
          <h4>Begrenzte Gre</h4>
          <p>Festgelegt bei JVM-Start. berschreitung fhrt zum StackOverflowError.</p>
        </div>
      </div>

      <div class="example-box">
        <h4>Praktisches Beispiel:</h4>
        <pre class="code-example">
public class StackExample {
    public static void main(String[] args) {
        int number = 42;
        boolean isActive = true;
        double pi = 3.14159;
        String text = "Hallo Welt";
        int[] numbers = {1, 2, 3, 4, 5};
        int result = calculate(number);
    }

    public static int calculate(int input) {
        int squared = input * input;
        return squared;
    }
}</pre>

        <div class="example-explanation">
          <p><strong>Stack-Status whrend der Ausfhrung:</strong></p>
          <pre>

 Stack Frame: calculate()       
 input = 42                     
 squared = 1764                 

 Stack Frame: main()            
 number = 42                    
 isActive = true                
 pi = 3.14159                   
 text  [Heap: String-Objekt]   
 numbers  [Heap: Array-Objekt] 
 result = ?                     

          </pre>
        </div>
      </div>

      <div class="flex-container">
        <div class="important-note">
          <h4>Wichtig zu wissen:</h4>
          <ul>
            <li><strong>Primitive vs. Referenzen:</strong> Primitive Werte liegen direkt im Stack. Referenzen zeigen auf Heap-Objekte.</li>
            <li><strong>Stack pro Thread:</strong> Jeder Thread hat seinen eigenen Stack.</li>
            <li><strong>Kein Garbage Collection:</strong> Speicher wird automatisch freigegeben.</li>
          </ul>
        </div>

        <div class="summary-section">
          <h3>Zusammenfassung:</h3>
          <p>Der Memory Stack ist zustndig fr die Verwaltung von lokalen Variablen und Methodenaufrufen. Durch seine automatische Speicherverwaltung und hohe Geschwindigkeit ist er ein zentraler Bestandteil der JVM-Laufzeitumgebung.</p>
        </div>
      </div>

      <div class="comparison-section">
        <h3>Stack vs. Heap im berblick:</h3>
        <div class="table-container">
          <table class="comparison-table">
            <thead>
              <tr>
                <th>Eigenschaft</th>
                <th>Stack</th>
                <th>Heap</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td><strong>Gespeicherte Daten</strong></td>
                <td>Primitive Typen, Referenzen</td>
                <td>Objekte, Arrays, Strings</td>
              </tr>
              <tr>
                <td><strong>Lebensdauer</strong></td>
                <td>Whrend Methodenausfhrung</td>
                <td>Bis keine Referenzen mehr existieren</td>
              </tr>
              <tr>
                <td><strong>Zugriff</strong></td>
                <td>Jeder hat zugriff auf alles im Heap</td>
                <td>Zugriff nur auf eigenen Stack-Frame (Funktionsbereich)</td>
              </tr>
              <tr>
                <td><strong>Speicherfreigabe</strong></td>
                <td>Automatisch (LIFO)</td>
                <td>Garbage Collector</td>
              </tr>
              <tr>
                <td><strong>Grsse</strong></td>
                <td>Fest vorgegeben</td>
                <td>Dynamisch (kann wachsen)</td>
              </tr>
              <tr>
                <td><strong>Zugriffsgeschwindigkeit</strong></td>
                <td>Sehr schnell, ist kleiner</td>
                <td>Langsamer als Stack, ist grsser, muss durchsucht werden</td>
              </tr>
              <tr>
                <td><strong>Fehler bei berlauf</strong></td>
                <td>StackOverflowError</td>
                <td>OutOfMemoryError</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </TheoryLayout>
</template>

