import { defineStore } from 'pinia'

export const useMemoryStore = defineStore('memory', {
  state: () => ({
    currentLine: 0,          // Die zuletzt ausgefhrte Zeile
    nextLineToExecute: 1,    // Die nchste auszufhrende Zeile (= instructionPointer)
    stackVariables: [],
    heapObjects: [],
    stackPointer: -1,
    history: [],
    canGoBack: false,
    programCompleted: false, // Neuer Status fr Programmende
    currentCode: `public class Example {
    public static void main(String[] args) {
        int x = 5;
        String message = "Hello";
        System.out.println(message + " World!");
    }
}`
  }),
  
  actions: {
    saveState() {
      this.history.push({
        currentLine: this.currentLine,
        nextLineToExecute: this.nextLineToExecute,
        stackVariables: JSON.parse(JSON.stringify(this.stackVariables || [])),
        heapObjects: JSON.parse(JSON.stringify(this.heapObjects || [])),
        stackPointer: this.stackPointer,
        programCompleted: this.programCompleted
      });
      this.canGoBack = true;
    },

    nextLine() {
      // Zustand speichern
      this.saveState();
      
      const maxLines = this.currentCode.split('\n').length;
      
      // Wenn wir eine nchste Zeile haben
      if (!this.programCompleted && this.nextLineToExecute < maxLines) {
        // Die aktuelle Zeile ist die, die wir jetzt ausfhren
        this.currentLine = this.nextLineToExecute;
        
        // Vorbereitung fr die nchste auszufhrende Zeile
        if (this.currentLine < maxLines - 1) {
          this.nextLineToExecute = this.currentLine + 1;
        } else {
          // Programmende erreicht
          this.programCompleted = true;
        }
        
        // Speichernderungen
        if (this.currentLine === 2) {
          this.stackVariables.push({ name: 'x', value: 5, address: '0x001', type: 'int' });
          this.stackPointer = 0;
        }
        else if (this.currentLine === 3) {
          this.stackVariables.push({ name: 'message', value: '@0x123', address: '0x002', type: 'String' });
          this.heapObjects.push({ id: '0x123', type: 'String', content: 'Hello', size: 5 });
          this.stackPointer = 1;
        }
        
        return true;
      }
      
      return false;
    },
    
    previousLine() {
      if (this.history && this.history.length > 0) {
        const prevState = this.history.pop();
        
        this.currentLine = prevState.currentLine;
        this.nextLineToExecute = prevState.nextLineToExecute;
        this.stackVariables = prevState.stackVariables;
        this.heapObjects = prevState.heapObjects;
        this.stackPointer = prevState.stackPointer;
        this.programCompleted = prevState.programCompleted;
        
        this.canGoBack = this.history.length > 0;
        return true;
      }
      
      this.canGoBack = false;
      return false;
    },
    
    reset() {
      this.currentLine = 0;       // Keine Zeile wurde noch ausgefhrt
      this.nextLineToExecute = 1; // Die erste Zeile wird als nchstes ausgefhrt
      this.stackPointer = -1;
      this.stackVariables = [];
      this.heapObjects = [];
      this.history = [];
      this.canGoBack = false;
      this.programCompleted = false;
    }
  }
})