<template>
  <TheoryLayout title="Method Area">
    <div class="theory-content">
      <h2>Was ist die Method Area?</h2>
      <p>Die Method Area (auch als Metadatenbereich bekannt) ist ein Teil des JVM-Speichers, der Klasseninformationen und statische Daten speichert.</p>
      
      <div class="visual-container">
        <div class="visual-fallback">
          <pre>

           METHOD AREA               
                                     
      
   Klasse: Student                 
   - Statische Felder              
   - Methodencode                  
   - Konstantenpool                
      
                                     
      
   Klasse: Calculator              
   - Statische Felder              
   - Methodencode                  
   - Konstantenpool                
      
                                     
      
   Klasse: Course                  
   - Statische Felder              
   - Methodencode                  
   - Konstantenpool                
      
                                     

          </pre>
        </div>
      </div>

      <h3>Inhalte der Method Area:</h3>
      <div class="content-grid">
        <div class="content-item">
          <h4>1. Klassenmetadaten</h4>
          <ul>
            <li>Vollstndiger Klassenname und Paketinformation</li>
            <li>Vererbungshierarchie (Superklassen, implementierte Interfaces)</li>
            <li>Informationen ber Konstruktoren und Methoden</li>
            <li>Typinformationen und Zugriffsbeschrnkungen</li>
            <li>Annotations und andere Metadaten</li>
          </ul>
        </div>
        
        <div class="content-item">
          <h4>2. Statische Variablen</h4>
          <ul>
            <li>Alle mit <code>static</code> deklarierten Felder</li>
            <li>Klassenweite Konstanten (<code>static final</code>)</li>
            <li>Datenwerte, die fr alle Instanzen gleich sind</li>
            <li>Beispiel: <code>Math.PI</code>, <code>Integer.MAX_VALUE</code></li>
          </ul>
        </div>
        
        <div class="content-item">
          <h4>3. Methoden-Bytecode</h4>
          <ul>
            <li>Kompilierter Code aller Methoden (JVM Bytecode)</li>
            <li>Ausfhrungsinstruktionen fr die JVM</li>
            <li>Statische und Instanz-Methodenimplementierungen</li>
            <li>Nativer Code-Verknpfungen (JNI)</li>
          </ul>
        </div>
        
        <div class="content-item">
          <h4>4. Konstantenpool</h4>
          <ul>
            <li>String-Literale und numerische Konstanten</li>
            <li>Symbolische Verweise auf Klassen und Methoden</li>
            <li>Feld- und Methodennamen</li>
            <li>Typ-Deskriptoren und Signaturen</li>
          </ul>
        </div>
      </div>

      <div class="example-box">
        <h4>Praktisches Beispiel:</h4>
        <pre class="code-example">
public class MethodAreaExample {
    // Statische Variable - in der Method Area gespeichert
    public static int counter = 0;
    
    // Statische Konstante - in der Method Area gespeichert
    public static final String APP_NAME = "Memory Visualizer";
    
    // Statische Methode - Bytecode in der Method Area
    public static void incrementCounter() {
        counter++;
        System.out.println("Counter: " + counter);
    }
    
    // Instanzmethode - Bytecode auch in der Method Area
    public void printMessage(String message) {
        // String-Literal "Message: " im Konstantenpool der Method Area
        System.out.println("Message: " + message);
    }
    
    public static void main(String[] args) {
        // Ruft Code aus der Method Area auf
        incrementCounter();
        
        // Erzeugt ein Objekt (im Heap),
        // aber die Klasseninformation kommt aus der Method Area
        MethodAreaExample example = new MethodAreaExample();
        example.printMessage("Hello World");
    }
}</pre>

        <div class="example-explanation">
          <p><strong>Was passiert im Hintergrund:</strong></p>
          <ol>
            <li>Beim Laden der Klasse <code>MethodAreaExample</code> werden alle Klasseninformationen in der Method Area gespeichert.</li>
            <li>Die statische Variable <code>counter</code> wird in der Method Area initialisiert.</li>
            <li>Die Konstante <code>APP_NAME</code> wird im Konstantenpool abgelegt.</li>
            <li>Der Bytecode fr alle Methoden wird in der Method Area gespeichert.</li>
            <li>Beim Aufruf von <code>new MethodAreaExample()</code> werden die Klasseninformationen aus der Method Area gelesen, um das Objekt im Heap zu erstellen.</li>
          </ol>
        </div>
      </div>
      
      <div class="comparison-section">
        <h3>Method Area vs. Heap vs. Stack:</h3>
        <div class="table-container">
          <table class="comparison-table">
            <thead>
              <tr>
                <th>Speicherbereich</th>
                <th>Gespeicherte Daten</th>
                <th>Lebensdauer</th>
                <th>Speicherverwaltung</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td><strong>Method Area</strong></td>
                <td>Klassenmetadaten, statische Variablen, Bytecode</td>
                <td>Solange die Klasse geladen ist</td>
                <td>Wird bei Klassenladen gefllt, bei Klassenentladen geleert</td>
              </tr>
              <tr>
                <td><strong>Heap</strong></td>
                <td>Objekte, Arrays, Instanzvariablen</td>
                <td>Solange Objekte referenziert werden</td>
                <td>Garbage Collection</td>
              </tr>
              <tr>
                <td><strong>Stack</strong></td>
                <td>Lokale Variablen, Methodenparameter</td>
                <td>Whrend der Methodenausfhrung</td>
                <td>Automatisch bei Methodenende</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      
      <div class="flex-container">
        <div class="important-note">
          <h4>Wichtig zu wissen:</h4>
          <ul>
            <li><strong>JVM-Spezifisch:</strong> Die genaue Implementierung der Method Area kann je nach JVM-Implementierung variieren.</li>
            <li><strong>Permanente Generation:</strong> In lteren Java-Versionen war die Method Area Teil der "Permanent Generation". Ab Java 8 wurde sie durch den "Metaspace" ersetzt, der dynamisch wachsen kann.</li>
            <li><strong>OutOfMemoryError:</strong> Wenn zu viele Klassen geladen werden, kann ein "OutOfMemoryError: Metaspace" auftreten.</li>
            <li><strong>Klassenlader:</strong> Die Method Area wird durch Klassenlader (ClassLoader) befllt, wenn neue Klassen bentigt werden.</li>
          </ul>
        </div>

        <div class="summary-section">
          <h3>Zusammenfassung:</h3>
          <p>Die Method Area ist das "Wissensarchiv" der JVM. Sie enthlt alle Informationen ber Klassen, ihre Methoden, statische Variablen und Konstanten. Ohne die Method Area wsste die JVM nicht, wie Objekte strukturiert sind oder wie Methoden ausgefhrt werden sollen. Sie ist ein zentraler Bestandteil der Java-Speicherarchitektur und ermglicht erst die Ausfhrung von Java-Programmen.</p>
        </div>
      </div>
      
      <h3>Funktion im Gesamtsystem:</h3>
      <p>Die Method Area ist fr die Laufzeitumgebung wichtig, da sie Informationen ber Klassen und Methoden bereitstellt, die fr die Ausfhrung bentigt werden. Im Gegensatz zum Heap, der Instanzen von Klassen speichert, enthlt die Method Area die Klasseninformationen selbst.</p>
    </div>
  </TheoryLayout>
</template>

