        <div class="card-footer">
          <span class="read-more">Mehr lesen &rarr;</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TheoryCards',
  data() {
    return {
      theoryCards: [
        {
          id: 'memory-heap',
          title: 'Memory Heap',
          shortDescription: 'The heap is where objects are stored. When you create an object using \'new\', it\'s allocated on the heap.'
        },
        {
          id: 'memory-stack',
          title: 'Memory Stack',
          shortDescription: 'The stack stores local variables and method call information. Each thread has its own stack.'
        },
        {
          id: 'callstack',
          title: 'Call Stack',
          shortDescription: 'The call stack keeps track of method calls. Each method call creates a new frame on the stack.'
        },
        {
          id: 'stack-pointer',
          title: 'Stack Pointer',
          shortDescription: 'Points to the top of the stack, indicating the current execution context.'
        },
        {
          id: 'instruction-pointer',
          title: 'Instruction Pointer',
          shortDescription: 'Points to the next instruction to be executed in the program.'
        },
        {
          id: 'method-area',
