<template>
  <TheoryLayout title="Instruction Pointer">
    <div class="theory-content">
      <h2>Was ist der Instruction Pointer?</h2>
      <p>Der Instruction Pointer (auch Program Counter oder PC genannt) ist ein Register in der JVM, das auf die aktuelle oder nchste auszufhrende Anweisung zeigt.</p>
      
      <h3>Aufgaben:</h3>
      <ul class="compact-list">
        <li>Er verfolgt den Fortschritt der Programmausfhrung</li>
        <li>Er zeigt auf die nchste auszufhrende Bytecode-Instruktion</li>
        <li>Er wird nach jeder Instruktion aktualisiert</li>
      </ul>
      
      <h3>Programmflusssteuerung:</h3>
      <p>Der Instruction Pointer ist entscheidend fr die Steuerung des Programmflusses. Bei Sprngen, bedingten Anweisungen oder Schleifenausfhrungen wird der Instruction Pointer entsprechend angepasst, um auf die nchste auszufhrende Anweisung zu zeigen.</p>
      
      <h3>Thread-spezifisch:</h3>
      <p>In Java hat jeder Thread seinen eigenen Instruction Pointer, damit mehrere Threads gleichzeitig an verschiedenen Stellen im Code ausgefhrt werden knnen.</p>

      <div class="visual-container compact">
        <img src="/images/theory/instruction-pointer.png" alt="Instruction Pointer Visualisierung" class="theory-image" onerror="this.style.display='none'; document.getElementById('instruction-pointer-fallback').style.display='block';" />
        <div id="instruction-pointer-fallback" class="visual-fallback" style="display:none;">
          <pre>

 Bytecode einer Methode:             
                                     
 0: iload_1         // lade x        
 1: iload_2         // lade y        
 2: iadd            // addiere         Instruction Pointer
 3: istore_3        // speichere z   
 4: iload_3         // lade z        
 5: ireturn         // kehre zurck  
                                     

          </pre>
        </div>
      </div>

      <h3>Hauptaufgaben des Instruction Pointers:</h3>
      <ul class="compact-list">
        <li><strong>Ausfhrungsverfolgung:</strong> Zeigt immer auf die nchste auszufhrende Instruktion</li>
        <li><strong>Kontinuitt:</strong> Ermglicht sequentielle Abarbeitung von Anweisungen</li>
        <li><strong>Sprungsteuerung:</strong> Wird bei Verzweigungen (if/else), Schleifen und Methodenaufrufen angepasst</li>
        <li><strong>Kontrolltransfer:</strong> Bei Exceptions oder return-Anweisungen wird er entsprechend aktualisiert</li>
      </ul>
      
      <div class="comparison-note">
        <h4>Unterschied zum Stack Pointer:</h4>
        <div class="comparison-grid">
          <div class="comparison-item">
            <h5>Stack Pointer (SP)</h5>
            <ul class="compact-list">
              <li>Zeigt auf den aktuellen Stack-Frame</li>
              <li>Verfolgt den Kontext der Methode</li>
              <li>Bezieht sich auf den Call Stack</li>
              <li>ndert sich nur bei Methoden-Ein-/Austritt</li>
            </ul>
          </div>
          <div class="comparison-item">
            <h5>Instruction Pointer (IP)</h5>
            <ul class="compact-list">
              <li>Zeigt auf eine konkrete Anweisung</li>
              <li>Verfolgt den Fortschritt in der Methode</li>
              <li>Bezieht sich auf Bytecode-Anweisungen</li>
              <li>ndert sich bei jeder Anweisungsausfhrung</li>
            </ul>
          </div>
        </div>
      </div>

      <div class="instruction-animation">
        <h3>Der Instruction Pointer in Aktion:</h3>
        <p>Betrachten wir, wie sich der Instruction Pointer bei der Ausfhrung einer einfachen if-else-Struktur bewegt:</p>
        
        <div class="code-with-ip">
          <pre class="code-example">
public int vergleiche(int a, int b) {
    if (a &gt; b) {
        return a;
    } else {
        return b;
    }
}</pre>

          <div class="bytecode-execution">
            <p><strong>Vereinfachter Bytecode mit IP-Bewegung:</strong></p>
            <div class="execution-steps">
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>0: iload_1       // lade a</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>1: iload_2       // lade b</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>2: if_icmple 7   // wenn a &lt;= b, springe zu 7</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>5: iload_1       // lade a</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>6: ireturn       // kehre mit a zurck</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>7: iload_2       // lade b</code>
              </div>
              <div class="execution-step">
                <div class="ip-indicator"></div>
                <code>8: ireturn       // kehre mit b zurck</code>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="important-note">
        <h4>Wichtig zu wissen:</h4>
        <ul class="compact-list">
          <li><strong>Unsichtbar fr Entwickler:</strong> Der Instruction Pointer ist im Java-Code nicht direkt sichtbar oder manipulierbar.</li>
          <li><strong>JVM-intern:</strong> Er wird ausschlielich von der JVM verwaltet, um den Ausfhrungsfluss zu steuern.</li>
          <li><strong>Debug-Tools:</strong> Debugger knnen den aktuellen Instruction Pointer anzeigen, um die Programmausfhrung zu verfolgen.</li>
          <li><strong>Multithreading:</strong> Bei Kontext-Wechseln zwischen Threads wird der jeweilige Instruction Pointer gespeichert und wiederhergestellt.</li>
        </ul>
      </div>

      <div class="summary-section">
        <h3>Zusammenfassung:</h3>
        <p>Der Instruction Pointer ist ein fundamentales Konzept in der Programmausfhrung und ein wesentlicher Bestandteil der JVM-Architektur. Er steuert den Programmablauf, indem er stets auf die nchste auszufhrende Anweisung zeigt. Ohne den Instruction Pointer knnte die JVM keine sequentielle Ausfhrung von Code oder bedingte Sprnge durchfhren.</p>
      </div>
    </div>
  </TheoryLayout>
</template>

