<template>
  <TheoryLayout title="Call Stack">
    <!--bernimmt TheoryLayout und setzt den Titel auf "Call Stack"-->
    <div class="theory-content">
      <h2>Was ist der Call Stack?</h2>
      <p>Der Call Stack (Aufrufstapel) ist eine Datenstruktur, die von der Java Virtual Machine (JVM) verwendet wird, um den Ausfhrungsverlauf von Methodenaufrufen zu verfolgen. 
      </p>
      
      <!-- ASCI visualisierung Beispiel-->
      <div class="visual-container">
        <div class="visual-fallback">
          <pre>
          
               main() Frame          Aktueller Frame
          
               MethodeA() Frame    
          
               MethodeB() Frame    
          
          </pre>
        </div>
      </div>
      
      <h3>Funktionsweise:</h3>
      <ul>
        <li>Der Call Stack arbeitet nach dem Last In, First Out-Prinzip (LIFO) - die zuletzt aufgerufene Methode wird als erste beendet.</li>
        <li>Jeder Methodenaufruf erzeugt einen neuen Stack Frame, der auf den Call Stack gelegt wird.</li>
        <li>Ein Stack Frame enthlt:
          <ul>
            <li>Lokale Variablen der Methode</li>
            <li>Parameter, die an die Methode bergeben wurden</li>
            <li>Rcksprungadresse (wo die Ausfhrung fortgesetzt wird, wenn die Methode beendet ist)</li>
            <li>Operanden-Stack fr Zwischenberechnungen</li>
          </ul>
        </li>
        <li>Wenn eine Methode beendet wird, wird ihr Frame vom Stack entfernt und der Frame der aufrufenden Methode wird wieder aktiv.</li>
      </ul>
      
      <!-- Beispielcode-->
      <div class="example-box">
        <h4>Praktisches Beispiel:</h4>
        <pre class="code-example">
        public class CallStackExample {
          public static void main(String[] args) {
              System.out.println("Start im main");
              methodA();
              System.out.println("Zurck in main");
          }
          
          public static void methodA() {
              System.out.println("In methodA");
              methodB();
              System.out.println("Zurck in methodA");
          }
          
          public static void methodB() {
              System.out.println("In methodB");
              // Hier knnte ein weiterer Methodenaufruf stehen
          }
        }</pre>
        <div class="example-explanation">
          <p><strong>Call Stack Status whrend der Ausfhrung:</strong></p>
          <ol>
            <li>Zunchst ist nur der <code>main()</code>-Frame auf dem Stack</li>
            <li>Nach Aufruf von <code>methodA()</code> kommt ein Frame fr <code>methodA()</code> dazu</li>
            <li>Nach Aufruf von <code>methodB()</code> kommt ein Frame fr <code>methodB()</code> dazu</li>
            <li>Wenn <code>methodB()</code> endet, wird ihr Frame entfernt, und <code>methodA()</code> wird wieder aktiv</li>
            <li>Wenn <code>methodA()</code> endet, wird ihr Frame entfernt, und <code>main()</code> wird wieder aktiv</li>
          </ol>
        </div>
      </div>
      
      <h3>Der Call Stack in Aktion:</h3>
      <div class="animation-placeholder">
        <p>Hier seht ihr, wie sich der Call Stack whrend der Programmausfhrung Schritt fr Schritt verndert:</p>
        <div class="stack-animation-steps">
          <div class="animation-step">
            <div class="step-description">1. Programm startet</div>
            <pre>

     main() Frame          Aktueller Frame

            </pre>
          </div>
          <div class="animation-step">
            <div class="step-description">2. methodA() wird aufgerufen</div>
            <pre>

     methodA() Frame       Aktueller Frame

     main() Frame        

            </pre>
          </div>
          <div class="animation-step">
            <div class="step-description">3. methodB() wird aufgerufen</div>
            <pre>

     methodB() Frame       Aktueller Frame

     methodA() Frame     

     main() Frame        

            </pre>
          </div>
          <div class="animation-step">
            <div class="step-description">4. methodB() kehrt zurck</div>
            <pre>

     methodA() Frame       Aktueller Frame

     main() Frame        

            </pre>
          </div>
          <div class="animation-step">
            <div class="step-description">5. methodA() kehrt zurck</div>
            <pre>

     main() Frame          Aktueller Frame

            </pre>
          </div>
        </div>
      </div>
      
      <!-- interaktiver Demo-Bereich -->
      <h3>Interaktives Beispiel:</h3>
      <div class="interactive-demo">
        <div class="demo-container">
          <div class="code-section">
            <!-- pre (vorformatierter Txt darstellen)-->
            <pre class="code-example interactive-code">
<span :class="{ 'highlight-method': currentStep ===1 }">public class CallStackExample {</span>
<span :class="{ 'highlight-method': currentStep ===2 }">    public static void <span :class="{ 'highlight-active-method': currentStep >= 2 && currentStep <= 4 || currentStep >= 14, 'highlight-in-stack': currentStep >= 2 }">main</span>(String[] args) {</span>
<span :class="{ 'highlight-method': currentStep === 3 }">        System.out.println("Start im main");</span>
<span :class="{ 'highlight-method': currentStep === 4 }">        <span :class="{ 'highlight-active-method': currentStep === 4, 'highlight-in-stack': currentStep >= 5 && currentStep <= 13 }">methodA</span>();</span>
<span :class="{ 'highlight-method': currentStep === 14 }">        System.out.println("Zurck in main");</span>
<span :class="{ 'highlight-method': currentStep === 15 }">    }</span>
    
<span :class="{ 'highlight-method': currentStep === 5 }">    public static void <span :class="{ 'highlight-active-method': currentStep >= 5 && currentStep <= 7 || currentStep >= 11 && currentStep <= 13, 'highlight-in-stack': currentStep >= 5 && currentStep <= 13 }">methodA</span>() {</span>
<span :class="{ 'highlight-method': currentStep === 6 }">        System.out.println("In methodA");</span>
<span :class="{ 'highlight-method': currentStep === 7 }">        <span :class="{ 'highlight-active-method': currentStep === 7, 'highlight-in-stack': currentStep >= 8 && currentStep <= 10 }">methodB</span>();</span>
<span :class="{ 'highlight-method': currentStep === 12 }">        System.out.println("Zurck in methodA");</span>
<span :class="{ 'highlight-method': currentStep === 13 }">    }</span>
    
<span :class="{ 'highlight-method': currentStep === 8 }">    public static void <span :class="{ 'highlight-active-method': currentStep >= 8 && currentStep <= 10, 'highlight-in-stack': currentStep >= 8 && currentStep <= 10 }">methodB</span>() {</span>
<span :class="{ 'highlight-method': currentStep === 9 }">        System.out.println("In methodB");</span>
<span :class="{ 'highlight-method': currentStep === 10 }">    }</span>
<span :class="{ 'highlight-method': currentStep === 11 }">}</span></pre>
          </div>
          
          <div class="stack-section">
            <h4>Call Stack:</h4>
            <div class="stack-container">
              <transition-group name="stack-frame">
                <!-- methodB Frame: Erscheint in Schritt 8-10 -->
                <div v-if="currentStep >= 8 && currentStep <= 10" :key="'methodB'" 
                     class="stack-frame" :class="{ 'active-frame': currentStep >= 8 && currentStep <= 10 }">
                  methodB() Frame
                </div>
                
                <!-- methodA Frame: Erscheint in Schritt 5-13 -->
                <div v-if="currentStep >= 5 && currentStep <= 13" :key="'methodA'" 
                     class="stack-frame" :class="{ 'active-frame': (currentStep >= 5 && currentStep <= 7) || (currentStep >= 11 && currentStep <= 13) }">
                  methodA() Frame
                </div>
                
                <!-- main Frame: Erscheint in Schritt 2-15 -->
                <div v-if="currentStep >= 2 && currentStep <=14" :key="'main'" 
                     class="stack-frame" :class="{ 'active-frame': (currentStep >= 2 && currentStep <= 4) || currentStep >= 14 }">
                  main() Frame
                </div>
              </transition-group>
            </div>
            
            <div class="output-console">
              <h4>Console Output:</h4>
              <pre class="console-output">{{ getConsoleOutput() }}</pre>
            </div>
          </div>
        </div>
        
        <div class="demo-controls">
          <button @click="prevStep" :disabled="currentStep <= 1">Vorheriger Schritt</button>
          <span class="step-indicator">Schritt {{ currentStep }} von {{ totalSteps }}</span>
          <button @click="nextStep" :disabled="currentStep >= totalSteps">Nchster Schritt</button>
        </div>
        
        <div class="step-description">
          <strong>Schritt {{ currentStep }}:</strong> {{ getStepDescription() }}
        </div>
      </div>
      
      <div class="important-note">
        <h4>Wichtig zu wissen:</h4>
        <ul>
          <li><strong>StackOverflowError:</strong> Bei zu vielen verschachtelten Methodenaufrufen (z.B. bei unbegrenzter Rekursion) kann der Call Stack voll werden, was zu einem StackOverflowError fhrt.</li>
          <li><strong>Thread-spezifisch:</strong> Jeder Thread in Java hat seinen eigenen Call Stack. Daher knnen mehrere Threads unabhngig voneinander Methoden aufrufen.</li>
          <li><strong>Stack-Gre:</strong> Die maximale Stack-Gre kann in der JVM konfiguriert werden (mit dem Parameter -Xss).</li>
        </ul>
      </div>
      
      <h3>Bedeutung:</h3>
      <p>Der Call Stack ist essentiell fr die Nachverfolgung von Methodenaufrufen und ermglicht die Verschachtelung von Funktionsaufrufen. Bei rekursiven Aufrufen kann der Stack bei zu tiefer Rekursion berlaufen, was zu einem StackOverflowError fhrt.</p>
    </div>
  </TheoryLayout>
</template>

<script>
import TheoryLayout from './TheoryLayout.vue';

export default {
  name: 'CallStackTheory',
  components: {
    TheoryLayout
  },
  // Steuert Anzeige von Code, Stack-Frames und Konsolenausgaben
  data() {
    return {
      currentStep: 1,
      totalSteps: 15, 

      // Konsolenausgaben fr jeden Schritt
      consoleOutputs: [ 
        "", // Initialzustand
        "", // Schritt 1 
        "", // Schritt 2
        "Start im main", // Schritt 3: erstes println in main
        "Start im main", // Schritt 4 
        "Start im main", // Schritt 5
        "Start im main\nIn methodA", // Schritt 6: println in methodA
        "Start im main\nIn methodA", // Schritt 7
        "Start im main\nIn methodA", // Schritt 8
        "Start im main\nIn methodA\nIn methodB", // Schritt 9: println in methodB
        "Start im main\nIn methodA\nIn methodB", // Schritt 10
        "Start im main\nIn methodA\nIn methodB", // Schritt 11
        "Start im main\nIn methodA\nIn methodB\nZurck in methodA", // Schritt 12: zweites println in methodA
        "Start im main\nIn methodA\nIn methodB\nZurck in methodA", // Schritt 13
        "Start im main\nIn methodA\nIn methodB\nZurck in methodA\nZurck in main", // Schritt 14: zweites println in main
        "Start im main\nIn methodA\nIn methodB\nZurck in methodA\nZurck in main" // Schritt 15
      ],
      // Beschreibungen fr jeden Schritt
      stepDescriptions: [
        "", // Platzhalter fr 0-Index
        "Programmbeginn - Die Klasse wird geladen.", // Schritt 1
        "Die main-Methode wird vorbereitet. Der main-Frame wird auf den Call Stack gelegt.", // Schritt 2
        "Die erste Ausgabe in main() wird ausgefhrt.", // Schritt 3
        "Der Aufruf von methodA() wird vorbereitet.", // Schritt 4
        "Die methodA-Definition wird geladen. Der methodA-Frame wird auf den Call Stack gelegt.", // Schritt 5
        "Die erste Ausgabe in methodA() wird ausgefhrt.", // Schritt 6
        "Der Aufruf von methodB() wird vorbereitet.", // Schritt 7
        "Die methodB-Definition wird geladen. Der methodB-Frame wird auf den Call Stack gelegt.", // Schritt 8
        "Die Ausgabe in methodB() wird ausgefhrt.", // Schritt 9
        "methodB() ist beendet, der Frame wird vom Stack entfernt.", // Schritt 10
        "Die Ausfhrung kehrt zu methodA() zurck.", // Schritt 11
        "Die zweite Ausgabe in methodA() wird ausgefhrt.", // Schritt 12
        "methodA() ist beendet, der Frame wird vom Stack entfernt.", // Schritt 13
        "Die Ausfhrung kehrt zu main() zurck und die zweite Ausgabe wird ausgefhrt.", // Schritt 14
        "main() ist beendet, das Programm endet." // Schritt 15
      ]
    };
  },
  // Steuert die Navigation durch die Schritte
  methods: {
    // Schritte im Call Stack vorwrts/rckwrts bewegen
    prevStep() {
      if (this.currentStep > 1) {
        this.currentStep--;
      }
    },
    nextStep() {
      if (this.currentStep < this.totalSteps) {
        this.currentStep++;
      }
    },
    // Konsolenausgabe und Schrittbeschreibung fr den aktuellen Schritt
    getConsoleOutput() {
      return this.consoleOutputs[Math.min(this.currentStep, this.consoleOutputs.length - 1)];
    },
    getStepDescription() {
      return this.stepDescriptions[this.currentStep];
    }
  }
}
</script>

