}

/* Animation fr Stack Frames */
.stack-frame-enter-active, .stack-frame-leave-active {
  transition: all 0.5s ease;
}

.stack-frame-enter-from {
  opacity: 0;
  transform: translateY(-20px);
}

.stack-frame-leave-to {
  opacity: 0;
