<template>
    <div class="memory-visualizer">
      <h2>Speichervisualisierung</h2>
      
      <div class="memory-sections">
        <div class="memory-section">
          <h3>Stack</h3>
          <div class="stack-area">
            <div 
              v-for="(variable, index) in store.stackVariables" 
              :key="index"
              :class="['stack-frame', { 'active-frame': store.stackPointer === index }]"
            >
              <div class="stack-var-name">{{ variable.name }}</div>
              <div class="stack-var-value">
                <span v-if="variable.type !== 'String'">{{ variable.value }}</span>
                <span v-else class="reference">{{ variable.value }}</span>
              </div>
              <div class="stack-var-type">{{ variable.type }}</div>
              <div class="stack-var-address">{{ variable.address }}</div>
            </div>
            <div v-if="store.stackVariables.length === 0" class="empty-message">
              Stack ist leer
            </div>
          </div>
        </div>
        
        <div class="memory-section">
          <h3>Heap</h3>
          <div class="heap-area">
            <div 
              v-for="object in store.heapObjects" 
              :key="object.id"
              class="heap-object"
            >
              <div class="object-header">{{ object.type }} ({{ object.id }})</div>
              <div class="object-content">"{{ object.content }}"</div>
              <div class="object-size">Size: {{ object.size }} bytes</div>
            </div>
            <div v-if="store.heapObjects.length === 0" class="empty-message">
              Heap ist leer
            </div>
          </div>
        </div>
      </div>
      
      <div class="pointers">
        <div class="pointer">
          <span class="pointer-label">Instruction Pointer (IP):</span>
          <span class="pointer-value"> {{ store.programCompleted ? 'Programm beendet' : 'Line ' + (store.nextLineToExecute + 1) }}</span>
        </div>
        <div class="pointer">
          <span class="pointer-label">Stack Pointer (SP):</span>
          <span class="pointer-value">
            {{ store.stackPointer >= 0 ? `Variable ${store.stackVariables[store.stackPointer]?.name}` : 'None' }}
          </span>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { useMemoryStore } from '../store/memory'
  
  export default {
    name: 'MemoryVisualizer',
    setup() {
      const store = useMemoryStore()
      return { store }
    }
  }
  </script>
  
  <style scoped>
  .memory-visualizer {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    background-color: white;
  }
  
  .memory-sections {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .memory-section {
    flex: 1;
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 10px;
  }
  
  .stack-area, .heap-area {
    min-height: 200px;
  }
  
  .stack-frame {
    background-color: white;
    border: 1px solid #ddd;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 4px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
  }
  
  .active-frame {
    border: 2px solid #4CAF50;
    background-color: #e8f5e9;
  }
  
  .stack-var-name, .stack-var-type {
    font-weight: bold;
  }
  
  .stack-var-address {
    color: #666;
    font-size: 0.9em;
  }
  
  .reference {
    color: #e83e8c;
    font-weight: bold;
  }
  
  .heap-object {
    background-color: white;
    border: 1px solid #ddd;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 4px;
  }
  
  .object-header {
    font-weight: bold;
    color: #0066cc;
    margin-bottom: 5px;
  }
  
  .object-size {
    font-size: 0.9em;
    color: #666;
  }
  
  .pointers {
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 4px;
  }
  
  .pointer {
    margin-bottom: 5px;
  }
  
  .pointer-label {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .empty-message {
    color: #999;
    text-align: center;
    padding: 20px;
  }
  </style>